.searchForm {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-bottom: 20px;
  padding: 8px;
  background-color: var(--primary-color);
  border-radius: 2px;
}

.input {
  padding: 8px;
  font-size: 18px;
  -ms-flex-positive: 1;
      flex-grow: 1;
  color: var(--secondary-color);
  outline: 0;
  border: 0;
  border-radius: 6px;
}

.button {
  padding: 8px 16px;
  font-size: 16px;
  font-weight: 700;
  color: var(--secondary-color);
  background-color: var(--primary-color);
  text-transform: uppercase;
  outline: 0;
  border: 0;
  cursor: pointer;
}

.button:hover {
  color: var(--white-color);
}

.input:focus {
  -webkit-box-shadow: 0 0 25px 2px rgba(6, 87, 26, 0.52);
          box-shadow: 0 0 25px 2px rgba(6, 87, 26, 0.52);
  -webkit-transition: -webkit-box-shadow .3s ease-out;
  transition: -webkit-box-shadow .3s ease-out;
  -o-transition: box-shadow .3s ease-out;
  transition: box-shadow .3s ease-out;
  transition: box-shadow .3s ease-out, -webkit-box-shadow .3s ease-out;
}



